.section {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sideBar {
  transition: 0.3s;
  position: fixed;
  background: #fff;
  left: -100%;
  z-index: 5;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.active {
  left: 0;
}

.header {
  border-bottom: 1px solid var(--gray-color);
}

.blockContent {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content {
  margin-bottom: 30px;
}

.footer {
  width: 100%;
  box-shadow: 1px 0px 1px rgba(90, 97, 105, 0.11),
    0px 2px 4px rgba(90, 97, 105, 0.12), 0px 5px 5px rgba(90, 97, 105, 0.06),
    0px 3.5px 35px rgba(90, 97, 105, 0.1);
}

@media (min-width: 540px) {
  .sideBar {
    width: 285px;
    height: 100%;
  }
  .content {
    flex-grow: 1;
  }
}

@media (min-width: 1024px) {
  .section {
    flex-direction: row;
  }
  .sideBar {
    position: static;
    min-width: 285px;
  }
  .blockContent {
    flex-grow: 1;
  }
}
